.wp-block-vml-fixtures-admin {
	background-color: #f3f3f3;
	border: 1px solid #ccc;
	padding: 20px;
	margin-bottom: 20px;
	.shelf {
		display: flex;
		flex-wrap: wrap;
		border: 1px solid #ccc;
		align-items: center;
		justify-content: center;
		h2 {
			flex-basis: 100%;
		}
		.group-position-8-2 {
			flex-basis: 100%;
		}
		.item-group {
			margin: 0 0.5em;

			.item {
				border: 1px solid #000;
				position: relative;
				margin: 0.5em auto;
				background-size: cover;

				.smallp {
					font-size: 0.8em;
					margin: 0;
					line-height: 0.8em;
				}
				.details {
					opacity: 0;
					transition: opacity 0.3s;
					background-color: #333;
					color: #fff;
					position: absolute;
					top: 0.5em;
					left: 0.5em;
					width: max-content; /* Let the width adjust according to content */
					z-index: 10; /* To ensure it appears above other elements */
					padding: 10px;
					border-radius: 4px; /* Optional rounded corners */
					display: none; /* Initially hide it */
					font-size: 0.8em;
				}
				&:hover .details {
					opacity: 1;
					display: block; /* Show on hover */
				}
			}
		}
	}
}
